import React from 'react'
import Header from './header.js'
import { Card,Form, Button} from 'react-bootstrap'

export default class Settings extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      "name" : "",
      "ip": "",
      "port": "",
      "type" : "",
      "username": "",
      "password": ""
    }
  }

  handleNameChange = (event) => {
    this.setState({"name": event.target.value})
  }

  handleIpChange = (event) => {
    this.setState({"ip": event.target.value})
  }

  handlePortChange = (event) => {
    this.setState({"port": event.target.value})
  }

  handleTypeChange = (event) => {
    this.setState({"type": event.target.value})
  }

  handleUsernameChange = (event) => {
    this.setState({"username": event.target.value})
  }

  handlePasswordChange = (event) => {
    this.setState({"password": event.target.value})
  }

  handleSubmit = (event) => {
    this.props.addChannel({"name": this.state.name, "ip": this.state.ip, "port": this.state.port, "type": this.state.type, "username": this.state.username, "password": this.state.password})
    // this.props.history.push('/home')
    alert("Success");
    this.setState({
      "name" : "",
      "ip": "",
      "port": "",
      "type": "",
      "username": "",
      "password": ""
    })
    event.preventDefault();
  }

  render(){
    return(
      <div>
        <Header/>
        <br/><br/>
         <Card style={{"width":"50%", "margin":"auto"}}>
          <Card.Body>
          <Form onSubmit={this.handleSubmit}>
            <Form.Group  controlId="channelName">
              <Form.Label>Channel Name</Form.Label>&nbsp;:&nbsp;
              <Form.Control required type="text" value={this.state.name} onChange={this.handleNameChange} />
            </Form.Group>
           &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <Form.Group  controlId="channelIp">
              <Form.Label>Channel IP</Form.Label>&nbsp;:&nbsp;
              <Form.Control required pattern="^([0-9]{1,3}\.){3}[0-9]{1,3}$" value={this.state.ip} onChange={this.handleIpChange}  />
            </Form.Group>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
             <Form.Group  controlId="channelPort">
               <Form.Label>Channel port</Form.Label>&nbsp;:&nbsp;
               <Form.Control required type="number" value={this.state.port} onChange={this.handlePortChange} />
             </Form.Group>
            <br/>
            <Form.Group  controlId="channelType">
              <Form.Label>Type</Form.Label>&nbsp;:&nbsp;
              <Form.Control required type="text" value={this.state.type} onChange={this.handleTypeChange}  />
            </Form.Group>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <Form.Group  controlId="channelUsername">
              <Form.Label>Username</Form.Label>&nbsp;:&nbsp;
              <Form.Control required type="text" value={this.state.username} onChange={this.handleUsernameChange}  />
            </Form.Group>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
             <Form.Group  controlId="channelPassword">
               <Form.Label>Password</Form.Label>&nbsp;:&nbsp;
               <Form.Control required type="password" value={this.state.password} onChange={this.handlePasswordChange}  />
             </Form.Group>
               &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
             <Button variant="primary" type="submit" >
              Submit
            </Button>
          </Form>
            </Card.Body>
          </Card>
      </div>
    )
  }
}
