import React from 'react';
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Home from './components/home.js'
import Settings from './components/settings.js'


class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      channelArr: []
    }
  }

  addChannel = (channel) => {
    console.log("test",channel)
    var tempChannelArr = JSON.parse(localStorage.getItem("channelArr")) ? JSON.parse(localStorage.getItem("channelArr")) : [];
    tempChannelArr.push(channel)
    console.log("tempChannel", tempChannelArr)
    this.setState({channelArr: tempChannelArr})
    localStorage.setItem("channelArr",JSON.stringify(tempChannelArr))

  }

  render() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path="/home" >
            <Home channelArr={this.state.channelArr} />
          </Route>
          <Route path="/settings" >
            <Settings addChannel={this.addChannel}/>
          </Route>
        </Switch>
      </div>
    </Router>
  ) };
}

export default App;
